@use 'variables' as v;
@use 'mixins' as m;

h1, h2, h3 {
    
}

h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
}
.text-center {
    text-align: center;
}

[class$="__heading"] {
    @include m.nombre-pagina;
    margin-top: 0;
    color: v.$blanco;
    @include m.desktop {
        margin-top: 3rem;
    }
    
}

[class$="__descripcion"] {
    @include m.descripcion-pagina;
    color: v.$blanco;
}
